import { Component, OnInit } from '@angular/core';
import { PurchasingSpbItemService, PurchasingPurchaseOrderItemService, PurchasingPurchaseOrderService, PurchasingPurchaseRequestItemService, PurchasingSpbService } from 'src/app/sdk/purchasing/services';
import { DirektoriPegawaiMasterService, CompanyKantorService } from 'src/app/sdk/hr/services';
import { AssetModelxService, VendorVendorService } from 'src/app/sdk/asset/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService, NzDrawerService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Spb, Spb_item, Purchase_request_item, Purchase_order } from 'src/app/sdk/purchasing/models';
import { Pegawai_master, Kantor } from 'src/app/sdk/hr/models';
import { Modelx, Vendor } from 'src/app/sdk/asset/models';
import { FormControl } from '@angular/forms';
import { SerachItemComponent } from '../serach-item/serach-item.component';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private purchasingSpbItemService: PurchasingSpbItemService,
        private purchasingPurchaseOrderItemService: PurchasingPurchaseOrderItemService,
        private purchasingPurchaseOrderService: PurchasingPurchaseOrderService,
        private purchasingPurchaseRequestItemService: PurchasingPurchaseRequestItemService,
        private purchasingSpbService: PurchasingSpbService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private vendorVendorService: VendorVendorService,
        private companyKantorService: CompanyKantorService,
        private assetModelxService: AssetModelxService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private drawerService: NzDrawerService
    ) { }

    dataSPB: Spb = {}
    list_item: Spb_item [] = []
    id_spb: string = ''

    listPegawai: Pegawai_master[] = []
    listKantor: Kantor[] = []
    listModel: Modelx[] = []
    listVendor: Vendor[] = []

    ngOnInit() {
        this.id_spb = this.activatedRoute.snapshot.paramMap.get('id_spb')
        this.getPreeData()
        this.getData()
        this.form()
        this.getListItem()

    }
    getPreeData() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(data => {
            this.listPegawai = data.data
        })

        this.companyKantorService.getCompanyKantors().subscribe(data => {
            this.listKantor = data.data
        })

        this.vendorVendorService.getVendorVendors().subscribe(data => {
            this.listVendor = data.data
        })
    }


    getData() {
        this.purchasingSpbService.getPurchasingSpbsId(this.id_spb).subscribe(
            data => {
                this.dataForm.setValue(data.data)
            }
        )
    }


    dataForm: FormGroupTypeSafe<Spb>
    form() {
        this.dataForm = this.fb.group<Spb>({
            id_spb: new FormControl(''),
            id_pegawai_penerima: new FormControl(''),
            id_vendor_pengirim: new FormControl(''),
            kantor_penerimaan: new FormControl(''),
            tanggal_penerimaan: new FormControl(''),
            id_dokumen: new FormControl(''),
            id_user: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            validasi: new FormControl(''),
            id_data_flow_detail: new FormControl(''),
            id_data_flow: new FormControl(''),
            status: new FormControl(''),
            posisi: new FormControl(''),
            nomor: new FormControl(''),
        })
    }

    // ## LIST ITEM -----------------------------------
    id_purchase_order_item: string = null;
    add_item() {
        const drawerRef = this.drawerService.create<SerachItemComponent, { value: string }, string>({
            nzTitle: 'Search PO Item',
            nzContent: SerachItemComponent,
            nzWidth: '900px',
            nzContentParams: {
                value: 'arifan'
            }
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {

            if (typeof data === 'string') {
                this.id_purchase_order_item = data;
                this.beforePOItem()
            }
        });
    }


    getListItem() {
        this.purchasingSpbItemService.getPurchasingSpbItems(JSON.stringify({id_spb: this.id_spb})).subscribe(
            data => {
                this.list_item = data.data
            }
        )
    }

    deleteItem(id) {
        this.spinner.show()
        this.purchasingSpbItemService.deletePurchasingSpbItemsId(id).subscribe(
            data=> {
                this.getListItem()
                this.spinner.hide()
            }
        )
    }


    dataItem: any = {};

    beforePOItem() {
        console.log("panggie ko")
        this.purchasingPurchaseOrderItemService.getPurchasingPurchaseOrderItemsId(this.id_purchase_order_item).subscribe(
            data => {
                this.dataItem.id_purchase_order_item = data.data.id_purchase_order_item;
                this.dataItem.id_purchase_order = data.data.id_purchase_order;
                this.dataItem.id_purchase_request_item = data.data.id_purchase_request_item;
                this.getPO(this.dataItem.id_purchase_order);
                this.getRequest(this.dataItem.id_purchase_request_item);
                console.log(this.dataItem)
            }
        )
    }

    dataRequest: Purchase_request_item = {}

    getRequest(id_purchase_request_item) {
        this.purchasingPurchaseRequestItemService.getPurchasingPurchaseRequestItemsId(id_purchase_request_item).subscribe(
            data=> {
                this.dataRequest = data.data
                this.getModel(this.dataRequest.id_model)
            }
        )
    }

    dataModel: Modelx = {}

    getModel(id_model) {
        this.assetModelxService.getAssetModelxesId(id_model).subscribe(
            data=> {
                this.dataModel = data.data
            }
        )
    }

    dataPO: Purchase_order = {}

    getPO(id_purchase_order) {
        this.purchasingPurchaseOrderService.getPurchasingPurchaseOrdersId(id_purchase_order).subscribe(
            data=> {
                this.dataPO = data.data
            }
        )
    }

    qty_diterima : number = 0
    simpan_item() {

        let param : Spb_item = {
            id_po_item : this.dataItem.id_purchase_order_item,
            qty_diterima : this.qty_diterima,
            id_spb : this.id_spb,
        };

        this.spinner.show()
        //simpan ka server
        this.purchasingSpbItemService.postPurchasingSpbItems(param).subscribe(
            data => {
                this.id_purchase_order_item = null;
                this.spinner.hide();
                this.getListItem()
            },
            err => {
                this.spinner.hide();
                this.pesan.pesanWarningForm('Dupikat pembelian Item!')
            }
        )
    }

    // ## END  LIST ITEM -----------------------------------



    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        if (this.list_item.length < 1) {
            this.pesan.pesanWarningForm('Anda harus pilih Item Pembelian!')
            this.spinner.hide()
            return
        }

        this.dataSPB = this.dataForm.value
        this.purchasingSpbService.putPurchasingSpbsId({ id: this.id_spb, body: this.dataSPB }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/purchasing/spb/penerimaan-barang/list'])
    }

}
