import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { PenerimaanBarangRoutingModule } from './penerimaan-barang-routing.module';
import { ListComponent } from './list/list.component';
import { AddComponent } from './add/add.component';
import { EditComponent } from './edit/edit.component';
import { DetailComponent } from './detail/detail.component';
import { SerachItemComponent } from './serach-item/serach-item.component';
import { NgZorroAntdModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { ApprovalModule } from 'src/app/components/approval/approval.module';



@NgModule({
    declarations: [ListComponent, AddComponent, EditComponent, DetailComponent, SerachItemComponent],
    imports: [
        CommonModule,
        PenerimaanBarangRoutingModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule,
        PerfectScrollbarModule,
        ApprovalModule
    ],
    providers: [
        FormBuilderTypeSafe
    ],
    entryComponents: [SerachItemComponent]
})
export class PenerimaanBarangModule { }
