import { StandarApprovalService, standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { VendorVendorService } from 'src/app/sdk/asset/services';
import { Component, OnInit } from '@angular/core';
import { PurchasingSpbItemService, PurchasingSpbService } from 'src/app/sdk/purchasing/services';
import { Spb, Spb_item } from 'src/app/sdk/purchasing/models';
import { Vendor } from 'src/app/sdk/asset/models';
import { DirektoriPegawaiMasterService, CompanyKantorService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Pegawai_master, Kantor } from 'src/app/sdk/hr/models';
import { ToolsLogFlowService } from 'src/app/sdk/user/services';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private purchasingSpbItemService: PurchasingSpbItemService,
        private purchasingSpbService: PurchasingSpbService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private companyKantorService: CompanyKantorService,
        private vendorVendorService: VendorVendorService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private standarApprovalService: StandarApprovalService,
        private toolsLogFlowService: ToolsLogFlowService,
    ) { }

    dataSpb: Spb = {}
    list_item: Spb_item[] = []
    id_spb: string = ''

    dataPegawai: Pegawai_master = {}
    dataKantor: Kantor = {}
    dataVendor: Vendor = {}

    ngOnInit() {
        this.activatedRoute.paramMap.subscribe(
            params => {
                console.log(params.get('id_spb'))
                if (params.get('id_spb')) {
                    this.id_spb = params.get('id_spb')
                }
                this.getDetailSpb()
                this.getDetailItem()
                this.getLog()

            }
        )



        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getDetailSpb()
                    this.getDetailItem()
                    this.getLog()
                }
            }
        )
    }

    params: standarFlow = null;
    id_data_flow: string = ''


    getDetailSpb() {
        this.spinner.show()
        this.purchasingSpbService.getPurchasingSpbsId(this.id_spb).subscribe(
            data => {
                this.dataSpb = data.data;
                this.getPegawai(data.data.id_pegawai_penerima);
                this.getkantor(data.data.kantor_penerimaan);
                this.getVendor(data.data.id_vendor_pengirim);
                this.spinner.hide()

                this.params = {
                    id: data.data.id_spb,
                    id_data_flow_detail: this.dataSpb.id_data_flow_detail,
                    id_data_flow: this.dataSpb.id_data_flow,
                    next_status: null,
                    primary_key: "id_spb"
                }
                this.standarApprovalService.param.next(this.params)
            }
        )
    }

    getPegawai(id_pegawai_penerima) {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMastersId(id_pegawai_penerima).subscribe(
            data => {
                this.dataPegawai = data.data;
            }
        )
    }
    getkantor(kantor_penerimaan) {
        this.companyKantorService.getCompanyKantorsId(kantor_penerimaan).subscribe(
            data => {
                this.dataKantor = data.data;
            }
        )
    }

    getVendor(id_vendor_pengirim) {
        this.vendorVendorService.getVendorVendorsId(id_vendor_pengirim).subscribe(
            data => {
                this.dataVendor = data.data;
            }
        )
    }

    getDetailItem() {
        this.purchasingSpbItemService.getPurchasingSpbItems(JSON.stringify({ id_spb: this.id_spb })).subscribe(
            res => {
                this.list_item = res.data;
                this.spinner.hide()
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_spb })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }

    goToBack() {
        this.router.navigate(['/purchasing/spb/penerimaan-barang/list'])
    }

}
